.app-container {
  position: absolute;
  /* top: 0; */
  width: 100%;
  height: 70px;
  bottom: 0%;
  z-index: 9999999;
}
.app-container2 {
  position: absolute;
  /* top: 0; */
  width: 100%;
  right:0px;
  height: 70px;
  bottom: 0%;
  z-index: 9999999;
}

.threejs-container{
  position: fixed;
  width:100%;
  height:100%;
}

#loading {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  -moz-transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  text-align: center;
  font-size: 20px;
  background: #fff;
  width: 150px;
  padding: 15px 20px;
  margin: 0 auto;
  display: block;

  pointer-events: none;
}

.imgclose5{
  width: 0.5rem;
  height: 0.5rem;
  position: absolute;
  right: -3%;
  top: 24%;
}


.mask-layer-begin{
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 1.0);
  z-index: 999999999;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.background-Animation2{
  animation: backgroundcolor2 3s  ease forwards ;
  -moz-animation:backgroundcolor2 3s  ease forwards ;
  -webkit-animation:backgroundcolor2 3s ease forwards ;
  -o-animation:backgroundcolor2 3s ease forwards ;
}
@keyframes backgroundcolor2{
  from {display:block;background-color: rgba(255,255,255,1.0);}
  to {display:none;background-color: rgba(255,255,255,0.0);}
}
@-moz-keyframes backgroundcolor2{
  from {display:block;background-color: rgba(255,255,255,1.0);}
  to {display:none;background-color: rgba(255,255,255,0.0);}
}
@-webkit-keyframes backgroundcolor2{
  from {display:block;background-color: rgba(255,255,255,1.0);}
  to {display:none;background-color: rgba(255,255,255,0.0);}
}
@-o-keyframes backgroundcolor2{
  from {display:block;background-color: rgba(255,255,255,1.0);}
  to {display:none;background-color: rgba(255,255,255,0.0);}
}




.background-Animation1{
  background-color: rgba(255,255,255,0.0);
  animation: backgroundcolor 2s  linear ;
  -moz-animation:backgroundcolor 2s  linear ;
  -webkit-animation:backgroundcolor 2s linear ;
  -o-animation:backgroundcolor 2s linear ;
}
@keyframes backgroundcolor{
  from {background-color: rgba(255,255,255,1.0);}
  to {background-color: rgba(255,255,255,0.0);}
}
@-moz-keyframes backgroundcolor{
  from {background-color: rgba(255,255,255,1.0);}
  to {background-color: rgba(255,255,255,0.0);}
}
@-webkit-keyframes backgroundcolor{
  from {background-color: rgba(255,255,255,1.0);}
  to {background-color: rgba(255,255,255,0.0);}
}
@-o-keyframes backgroundcolor{
  from {background-color: rgba(255,255,255,1.0);}
  to {background-color: rgba(255,255,255,0.0);}
} 


