.close-box {
    position:fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    top:25px;
    left:25px;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, 1);
    border: 2px solid rgba(0, 0, 0, 0.2);
}

.close-box>img {
    position: absolute;
    width: 60%;
    height: 60%;
    cursor: pointer;
}


@media only  screen and (min-width:600px) {
    .video-box {
        position: relative;
        top:-10%;
        width: 80%;
        height: fit-content;
    }
}


@media only  screen and (min-width:800px) {
    .video-box {
        position: relative;
        top:-10%;
        width: 60%;
        height: fit-content;
    }
}
