
.mask-new {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    /* opacity: 0; */
    z-index: 5;
    position: fixed;
    top: 0;
    left: 0;
    transition: opacity 0.3s ease;
    -moz-transition: opacity 0.3s ease;
    -webkit-transition: opacity 0.3s ease;
}

.popup_container{
    position: relative;
    top: 50%;
    left: 0;
    margin: 0 auto;
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    max-width: 800px;
    width: 90%;
    height: auto;
    background: #fff;
}

.popup_close {
    width: 23px;
    height: 23px;
    top: 10px;
    right: 10px;
    position: absolute;
    cursor: pointer;
    z-index: 2;
    background: #000;
    padding: 3px;
}

.cross {
    transform: rotate(45deg);
    -moz-transform: rotate(45deg);
    -webkit-transform: rotate(45deg);
    width: 100%;
    height: 100%;
    position: relative;
    top: 0;
    left: 0;
}
.cross:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 3px;
    height: 100%;
    background: #fff;
    margin-left: calc(50% - 1.5px);
}
.cross:after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: #fff;
    margin-top: calc(50% - 1.5px);
}
.popup_content {
    width: 100%;
    height: 100%;
}
.img_ratio {
    width: 100%;
    padding-top: 50%;
    position: relative;
}
.img_ratio_img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-size: 100% 100%;
}
.popup_text {
    font-family: 'InfinitiMetaPro-Normal', 'InfinitiBrand-Light';
    position: relative;
    top: 0;
    left: 0;
    margin: 30px;
    margin-top: 25px;
}
#popup_title, #popup_content_text {
    margin: 10px auto;
}
#popup_title {
    font-size: 19px;
}

